import React from 'react';


import EmpresaForm from '../Form';
import EmpresaFormModel from '../../../domain/empresa/EmpresaFormModel';
import AppPage from "../../../components/AppPage";

import {FormikHelpers} from "formik/dist/types";
import AlertModel from "../../../common/AlertModel";


interface Props {
  onSubmit: (values: any, formikHelpers: FormikHelpers<any>) => void | Promise<any>
  initialValues: EmpresaFormModel
  alert?: AlertModel
}

export default function EmpresaCadastrarView({onSubmit, alert, initialValues}: Props) {


  return (<AppPage
    alertModel={alert}
    header={<> <i className="fa fa-align-justify"/> Empresas</>}>
    <EmpresaForm onSubmit={onSubmit} initialValues={initialValues}/>
  </AppPage>);
}


