import React, {useState} from 'react';

import {RouteComponentProps} from 'react-router';
import {FormikHelpers} from 'formik/dist/types';
import EmpresaFormModel from '../../../domain/empresa/EmpresaFormModel';
import {cadastraEmpresa} from "../../../domain/empresa/EmpresaService";
import {AxiosError} from "axios";
import CadastrarView from './CadastrarView';
import AlertModel from "../../../common/AlertModel";
import handleAxiosError from "../../../common/handleAxiosError";


export default function EmpresaCadastrar({history}: RouteComponentProps) {
  const [alert, setAlert] = useState<AlertModel | undefined>(undefined);

  const onSubmit = (empresa: EmpresaFormModel, {setSubmitting}: FormikHelpers<EmpresaFormModel>) => {
    cadastraEmpresa(empresa)
      .then(() => history.push('/empresas'))
      .catch((e: AxiosError) => {
        setAlert({color: 'danger', message: handleAxiosError(e)})
        setSubmitting(false);
      });
  };

  const initialValues: EmpresaFormModel = {
    nome: '',
    cnpj: '',
    endereco: '',
    representante: '',
    telefone: '',
    email: '',
  };

  return (<CadastrarView {...{alert, onSubmit, initialValues}}/>);
}



